
body{
    margin: 0;
    padding: 0;
    background: url("C:\Users\Sampurna\Desktop\web dev\LGM\users_data\src/back-img.png");
    background-position: cover;
}
.App-body{
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
}
.navbar{
    display: flex;
    width: 100%;
    height:150px;
    align-items: center;
    justify-content:flex-start;
    /* background: #cf0728; */
    
    background: linear-gradient( #570257d3, #34076e7c); 
}
/* header containing nav bar and title  */
.header{
   margin-left: 40px;
   font-family:Verdana, Geneva, Tahoma, sans-serif;
   text-shadow:2px 2px #000000;
   color:white;
   
  }
  .header h1{
    font-size: 40px;
  }
  .header .logo-image{
      width: 100px;
      height: 50px;
      transition: width 2s, height 4s;
}

  
  .header .logo-image:hover {
    width: 150px;
    height: 70px;

  }
.nav-button{
 position:absolute;
 right: 2rem;
}
.nav-button .btn{
background-color: #100330;
color: white;
border: 2px solid black;
margin-right: 20px;
width: 11rem;
height: 30px;
border: 2px solid white;
border-radius: 5px;
text-transform: underline;
font-weight: 400;
font-family:Georgia, 'Times New Roman', Times, serif;
font-size: 13px;
transform : .5s;
}
.nav-button .btn:hover{
    background-color: #ca5682;
    width: 13rem;
    color: antiquewhite;
    

}

.wrapper-box{
    margin:30px 0;
    
  
}
.box{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    
}
 .box-data{
   
    background: linear-gradient(to right, #000000, #8a2745);
    width: 28%;
    padding: 2.5px;
    margin: 15px;
    border-radius: 13px;
    align-items: center;
}
.box-data .box-item{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: white;
   width: 80%;
   height: 100%;
    padding-top: 5px;
    border-radius: 12px;
    transform: .5s;
    box-shadow: 0 4px 8px 0 rgba(21, 167, 46, 0.856), 0 6px 20px 0 rgba(119, 67, 187, 0.19);;

}
.box-data .box-item:hover{
    width: 100%;
}
.box-data .box-item img{
    background-size: cover;
    height: 38%;
    width: 38%;
    border: 2px solid #8a2745;
  
}
.box-data .box-item .info{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.box-data .box-item .info h2{
    margin-top: 3px;
    font-size: 1.3rem;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
.box-data .box-item .info h2:hover{
    font-style: italic;
    color: #000000;
    font: 300;
    font-size: 1.7rem;
}
.box-data .box-item .info p{
    font-size: 0.7rem;
    color: #545455;
}
@media only screen and (max-width: 800px){
    .box-data{
        width: 43%;
    }
    .nav-button .btn{
        width: 6rem;
        font-size: 8px;
        margin-right: 15px;
    }
    .nav-button{
    right: 1rem;
    }
}

@media only screen and (max-width: 547px){
    .box-data{
        width: 70%;
    }
.navbar{
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.header{
    margin-left: 0;
    text-align: center;
}
.nav-button{
    position: static;
}
}